:root {
  --primary-color: #e94e0f;
  --primary-light-color: #f2642c;
  --primary-background-color: #fef6f3;
  --accent-color: #870b58;
  --accent-dark-color: #5e083d;
  --accent-light-color: #fbd0eb;
  --yellow-brand-color: #f8ad07;
  --accent-20-color: #e1c2d5;
  --info-color: #415fca;
  --info-light-color: hsl(227, 56%, 52%, 0.1);
  --success-color: #21bc5f;
  --success-light-color: hsla(144, 70%, 43%, 0.1);
  --warning-color: #ffc700;
  --warning-light-color: #FDEAC1;
  --danger-color: #ff005c;
  --danger-light-color: hsla(338, 100%, 50%, 0.8);
  --danger-lighter-color: hsla(338, 100%, 90%, 0.8);
  --brand-red-color: #E4003A;
  --brand-red-20-color: #F8BFCD;
  --brand-orange-color: #E94E0F;
  --brand-orange-20-color: #F9D2C3;
  --brand-yellow-color: #F8AD07;
  --brand-yellow-20-color: #FDEAC1;
  --brand-purple-color: #870B58;
  --brand-purple-20-color: #E1C2D5;
  --tuna-gray-color: #303642;
  --tuna-gray-80-color: #555d71;
  --tuna-gray-60-color: #7f8594;
  --tuna-gray-40-color: #aaaeb8;
  --tuna-gray-20-color: #f6f6f6;
  --slate-gray-color: #7783a0;
  --slate-gray-80-color: #929bb3;
  --slate-gray-60-color: #adb4c6;
  --slate-gray-40-color: #c8cdd9;
  --slate-gray-20-color: #e3e6ec;
  --slate-gray-10-color: #f1f3f5;
  --slate-gray-5-color: #f8fafc;
  --white-color: #ffffff;
  --black-color: #000000;
}

.list-group .list-group-item {
  padding: 2rem 0;
}
.list-group .list-group-item:first-child {
  padding-top: 0;
}

.list-group--compact .list-group-item {
  padding: 0.5rem 0;
}

.dropdown-item {
  display: flex;
  align-items: center;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}
.dropdown-item + .dropdown-item {
  border-top: 1px solid var(--slate-gray-10-color);
}
.dropdown-item.danger-zone {
  color: var(--danger-color);
}

.dropdown-menu {
  border-radius: 4px;
}

.dropdown-toggle {
  display: inline-flex;
  align-items: center;
  background: none;
  border: none;
  cursor: pointer;
}
.dropdown-toggle[aria-expanded=true]:after {
  transform: rotate(180deg);
}

.dropdown-toggle:not(.dropdown-toggle-split)::after {
  margin-left: 0.5rem !important;
}

.dropdown-toggle::after {
  width: 0.75rem;
  height: 0.5rem;
  margin-bottom: 0.0625rem;
  border: none !important;
  background-image: url("data:image/svg+xml,%3Csvg width='12' height='9' viewBox='0 0 12 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.48263 0.75L0 2.23263L6 8.23263L12 2.23263L10.5174 0.75L6 5.26737L1.48263 0.75Z' fill='%23AAAEB8'/%3E%3C/svg%3E");
  background-repeat: no-repeat;
  transition: transform 0.2s ease-in-out;
}

.dropdown-toggle.no-arrow::after {
  content: none;
}

*[class^=container],
*[class*=" container"] {
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  padding: 0 1.25rem;
}
@media screen and (max-width: 992px) {
  *[class^=container],
*[class*=" container"] {
    padding: 0 0.75rem;
  }
}

.container-base {
  width: 1280px;
  max-width: 100vw;
}

.container-lg {
  max-width: 1400px;
}

.toast-container .ngx-toastr {
  padding: 0.9375rem;
  box-shadow: 0 8px 24px rgba(15, 17, 21, 0.16);
}
.toast-container .ngx-toastr:hover {
  box-shadow: 0 16px 32px rgba(15, 17, 21, 0.1);
}

.toast-error,
.toast-info,
.toast-success,
.toast-warning {
  background-image: none;
}

.toast-error {
  background-color: var(--danger-color);
}

.alert {
  display: inline-block;
}

.sd-switch {
  height: 20px;
}
.sd-switch input:checked + i::after {
  margin-left: 12px;
}
.sd-switch i::before {
  background-color: var(--slate-gray-color);
}
.sd-switch i::after {
  width: 16px;
  height: 16px;
  left: 2px;
  top: 2px;
  bottom: 2px;
}

.sd-switch.is-disabled {
  opacity: 0.5;
  pointer-events: none;
}

.tooltip .tooltip-inner {
  max-width: 17.5rem;
  padding: 0.75rem 1rem;
  text-align: left;
  background-color: var(--tuna-gray-color);
  box-shadow: 0 16px 32px rgba(15, 17, 21, 0.1);
}
.tooltip .tooltip-arrow {
  border-bottom-color: var(--tuna-gray-color);
}
.tooltip.bs-tooltip-top .arrow::before {
  border-top-color: var(--tuna-gray-color);
}
.tooltip.bs-tooltip-bottom .arrow::before {
  border-bottom-color: var(--tuna-gray-color);
}
.tooltip.bs-tooltip-left .arrow::before {
  border-left-color: var(--tuna-gray-color);
}
.tooltip.bs-tooltip-right .arrow::before {
  border-right-color: var(--tuna-gray-color);
}

.modal {
  --desktop-padding: 2rem;
  --mobile-padding: 1.25rem;
}
.modal .modal-header,
.modal .modal-footer {
  border: none;
}
.modal .modal-content {
  padding: 1.75rem 0;
  box-shadow: 0 16px 32px rgba(15, 17, 21, 0.1);
  border: none;
}
.modal .modal-header {
  padding-top: 0;
  padding-bottom: 0.75rem;
  padding-left: var(--desktop-padding);
  padding-right: var(--desktop-padding);
}
@media screen and (max-width: 992px) {
  .modal .modal-header {
    padding-left: var(--mobile-padding);
    padding-right: var(--mobile-padding);
  }
}
.modal .modal-footer {
  flex-direction: column;
  align-items: flex-start;
  flex-wrap: nowrap;
  padding-top: 0.75rem;
  padding-bottom: 0;
  padding-left: var(--desktop-padding);
  padding-right: var(--desktop-padding);
}
@media screen and (max-width: 992px) {
  .modal .modal-footer {
    padding-left: var(--mobile-padding);
    padding-right: var(--mobile-padding);
  }
}
.modal .modal-body {
  padding: 0rem var(--desktop-padding);
}
@media screen and (max-width: 992px) {
  .modal .modal-body {
    padding: 0.75rem var(--mobile-padding);
  }
}
.modal .close {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: 2rem !important;
  height: 2rem !important;
  padding: 0 !important;
  margin: 0 !important;
  margin-left: auto !important;
  border-radius: 999px;
  border: none;
  background: none;
}
.modal .close svg {
  color: var(--tuna-gray-40-color);
}

.c-modal__title {
  color: var(--primary-color);
  font-size: 1rem;
  font-weight: 700;
  line-height: 1.2;
}

.c-modal__description {
  color: var(--tuna-gray-80-color);
  font-size: 0.875rem;
}

.c-modal .ui-button {
  max-height: 85%;
}

.modal-content {
  animation-name: modal;
  animation-duration: 0.2s;
  animation-timing-function: ease-out;
}

.modal-backdrop {
  animation-name: backdrop;
  animation-duration: 0.3s;
}

@keyframes modal {
  0% {
    opacity: 0;
    transform: scale(0.8);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes backdrop {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.5;
  }
}
[class*=u-brand-] .c-modal__title {
  color: var(--primary-dark-color);
}

