.form-group {
  display: flex;
  flex-direction: column;
  max-width: 100%;
  margin-bottom: 16px;
}
.form-group:last-child {
  margin-bottom: 0;
}

.form-footer {
  display: flex;
  justify-content: flex-end;
}
.form-footer button + button {
  margin-left: 8px;
}

.btn {
  justify-content: center;
}

.ng-select {
  width: 100%;
  height: 2.5rem;
}

.ng-select.ng-select-multiple .ng-select-container {
  min-height: 2.5rem;
  height: 100%;
}
.ng-select .ng-arrow-wrapper .ng-arrow {
  width: 0.5rem !important;
  height: 0.5rem !important;
}
.ng-select.ng-invalid:not(.ng-untouched):not(form):not([formgroupname]) {
  border-left: none !important;
}
.ng-select.ng-select-opened > .ng-select-container {
  border-color: var(--primary-color);
  box-shadow: none;
}
.ng-select.form-control-lg .ng-select-container {
  height: 2.5rem;
  min-height: 2.5rem;
}
.ng-select.auto-grow {
  display: flex;
  width: 100%;
}
.ng-select.auto-grow .ng-select-container {
  min-width: 100px;
  width: 100%;
}
.ng-select.auto-grow .ng-dropdown-panel {
  width: auto;
}
.ng-select .ng-select-container:hover .ng-clear-wrapper {
  height: 1.0625rem;
  width: 1.0625rem;
}
.ng-select .ng-select-container:hover .ng-clear-wrapper .ng-clear {
