}

.nav {
  margin: 0 -1rem;
}

.nav-link {
  position: relative;
  margin: 0 1rem;
  padding: 0.25rem 0;
  color: var(--tuna-gray-60-color);
  transition: box-shadow 0.15s ease-in-out;
}

.nav-link.active {
  font-weight: 700;
  color: var(--accent-color);
  box-shadow: inset 0 -2px 0 0 var(--yellow-brand-color);
}

.collapse-trigger {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 0.5rem 0;
  background: none;
  border: none;
  border-bottom: 1px solid var(--slate-gray-10-color);
}
.collapse-trigger .ui-icon {
  transition: transform 0.2s ease-in-out;
}
.collapse-trigger[aria-expanded=false] .ui-icon {
  transform: rotate(-90deg);
}

.collapse {
  transition: max-height 0.5s ease-in-out;
  overflow: hidden;
}
.collapse:not(.show) {
  display: block;
  max-height: 0;
  padding-top: 0;
}
.collapse.show {
  display: block;
  max-height: 1000px;
}

